
<template>
  <div class="mod-config">
    <el-form
      :inline="true"
      :model="dataForm"
      @keyup.enter.native="getDataList()"
    >
      <el-form-item>
        <el-input
          v-model="dataForm.key"
          placeholder="参数名"
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="getDataList()">查询</el-button>
        <el-button type="primary" @click="addOrUpdateHandle()">新增</el-button>
        <el-button
          type="danger"
          @click="deleteHandle()"
          :disabled="dataListSelections.length <= 0"
          >批量删除</el-button
        >
      </el-form-item>
    </el-form>
    <el-table
      :data="dataList"
      border
      v-loading="dataListLoading"
      @selection-change="selectionChangeHandle"
      style="width: 100%"
    >
      <el-table-column
        prop="paperName"
        header-align="center"
        align="center"
        label="试卷名称"
      >
      </el-table-column>
      <el-table-column
        prop="beginTime"
        header-align="center"
        align="center"
        label="开始时间"
      >
      </el-table-column>
      <el-table-column
        prop="endTime"
        header-align="center"
        align="center"
        label="结束时间"
      >
      </el-table-column>
      <el-table-column
        prop="totalTime"
        header-align="center"
        align="center"
        label="考试时长"
      >
      </el-table-column>
      <el-table-column
        prop="totalScore"
        header-align="center"
        align="center"
        label="总分"
      >
      </el-table-column>
      <el-table-column
        prop="actualScore"
        header-align="center"
        align="center"
        label="实际得分"
      >
      </el-table-column>

      <el-table-column
        prop="studentName"
        header-align="center"
        align="center"
        label="提交学生"
      >
      </el-table-column>

      <el-table-column
        prop="paperType"
        label="试卷状态"
        width="100"
        filter-placement="bottom-end"
      >
        <template slot-scope="scope">
          <el-tag disable-transitions>{{
            scope.row.paperType == 0 ? "待审批" : "已完成"
          }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        header-align="center"
        align="center"
        width="150"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="correct(scope.row.id, scope.row.studentName)"
            v-if="scope.row.paperType == 0"
            >审批</el-button
          >
          <el-button
            type="text"
            size="small"
            @click="showDonePaper(scope.row.id)"
            >查看</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页工具条 -->
    <el-pagination
      @size-change="sizeChangeHandle"
      @current-change="currentChangeHandle"
      :current-page="pageIndex"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pageSize"
      :total="totalPage"
      layout="total, sizes, prev, pager, next, jumper"
    >
    </el-pagination>

    <!-- 展示学生试卷 -->
    <el-dialog title="学生试卷" :visible.sync="dialogVisible" width="80%">
      <div id="pdfDom">
        <div class="top">
          <div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
          </div>
          <div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
            <div>-</div>
          </div>
        </div>

        <div class="paper-title">{{ donePaperEntity.paperName }}</div>
        <div class="paper-description">
          <!-- 试卷标题 -->
          <div>
            本试卷一共{{ subjectList.length }}题,总分为:{{
              donePaperEntity.totalScore
            }}分,考试时长:{{ donePaperEntity.totalTime }}分钟
          </div>
          <!-- 试卷描述信息 -->
          <div class="paper-description-right">
            <div class="studentName">
              答题人:<span class="inline">{{
                donePaperEntity.studentName
              }}</span>
            </div>
            <div>
              得分:<span class="inline">{{ donePaperEntity.actualScore }}</span>
            </div>
          </div>
        </div>
        <!-- 试卷题目  -->
        <!-- 判断题 -->
        <div class="judgement">
          <div class="subjectType">一、判断题</div>
          <!-- 判断题盒子 -->
          <div
            v-for="(item, i) in judgement"
            :key="i"
            :index="i"
            class="judgement-item"
          >
            <!-- 题目描述 -->
            <div>{{ i + 1 }} , {{ item.subjectName }}</div>
            <!-- 题目图片 -->

            <div><img :src="item.subjectImage" class="item-image" /></div>
            <div class="judgement-item-button">
              <div>
                <div>正确答案:{{ item.subjectAnwser }}</div>
                <div>
                  学生答案:{{
                    item.selectAnswer == "" ? "未作答" : item.selectAnswer
                  }}
                </div>
              </div>
              <div>
                <div>题目满分:{{ item.score }}</div>
                <div>学生得分:{{ item.actualScore }}</div>
              </div>
            </div>
          </div>
        </div>

        <!-- 单选题 -->
        <div class="single">
          <div class="subjectType">二、单选题</div>
          <!-- 判断题盒子 -->
          <div
            v-for="(item, i) in single"
            :key="i"
            :index="i"
            class="single-item"
          >
            <!-- 题目描述 -->
            <div>{{ i + 1 }} , {{ item.subjectName }}</div>
            <!-- 题目选项和图片 -->
            <div class="options-image">
              <!-- 选择题区域 -->
              <div class="options">
                <div>A:{{ item.aDescribtion }}</div>
                <div>B:{{ item.bDescribtion }}</div>
                <div>C:{{ item.cDescribtion }}</div>
                <div>D:{{ item.dDescribtion }}</div>
              </div>
              <!-- 图片区域 -->
              <div>
                <img :src="item.subjectImage" class="image" />
              </div>
            </div>
            <!-- 作答情况 -->
            <div class="single-item-button">
              <div>
                <div>正确答案:{{ item.subjectAnwser }}</div>
                <div>
                  学生答案:{{
                    item.selectAnswer == "" ? "未作答" : item.selectAnswer
                  }}
                </div>
              </div>
              <div>
                <div>题目满分:{{ item.score }}</div>
                <div>学生得分:{{ item.actualScore }}</div>
              </div>
            </div>
          </div>
        </div>

        <!-- 多选题 -->
        <div class="single">
          <div class="subjectType">三、多选题</div>
          <!-- 判断题盒子 -->
          <div
            v-for="(item, i) in noSingle"
            :key="i"
            :index="i"
            class="single-item"
          >
            <!-- 题目描述 -->
            <div>{{ i + 1 }} , {{ item.subjectName }}</div>
            <!-- 题目选项和图片 -->
            <div class="options-image">
              <!-- 选择题区域 -->
              <div class="options">
                <div>A:{{ item.aDescribtion }}</div>
                <div>B:{{ item.bDescribtion }}</div>
                <div>C:{{ item.cDescribtion }}</div>
                <div>D:{{ item.dDescribtion }}</div>
              </div>
              <!-- 图片区域 -->
              <div>
                <img :src="item.subjectImage" class="image" />
              </div>
            </div>
            <!-- 作答情况 -->
            <div class="single-item-button">
              <div>
                <div>正确答案:{{ item.subjectAnwser }}</div>
                <div>
                  学生答案:{{
                    item.selectAnswer == "" ? "未作答" : item.selectAnswer
                  }}
                </div>
              </div>
              <div>
                <div>题目满分:{{ item.score }}</div>
                <div>学生得分:{{ item.actualScore }}</div>
              </div>
            </div>
          </div>
        </div>

        <!-- 填空题 -->
        <div class="judgement">
          <div class="subjectType">四、填空题</div>
          <!-- 判断题盒子 -->
          <div
            v-for="(item, i) in fill"
            :key="i"
            :index="i"
            class="judgement-item"
          >
            <!-- 题目描述 -->
            <div>{{ i + 1 }} , {{ item.subjectName }}</div>
            <div><img :src="item.subjectImage" class="item-image" /></div>
            <div class="judgement-item-button">
              <div>
                <div>正确答案:{{ item.subjectAnwser }}</div>
                <div>
                  学生答案:{{
                    item.selectAnswer == "" ? "未作答" : item.selectAnswer
                  }}
                </div>
              </div>
              <div>
                <div>题目满分:{{ item.score }}</div>
                <div>学生得分:{{ item.actualScore }}</div>
              </div>
            </div>
          </div>
        </div>

        <!-- 主观题 -->
        <div class="judgement">
          <div class="subjectType">五、大题</div>
          <!-- 判断题盒子 -->
          <div
            v-for="(item, i) in shortAnswer"
            :key="i"
            :index="i"
            class="judgement-item"
          >
            <!-- 题目描述 -->
            <div>{{ i + 1 }} , {{ item.subjectName }}</div>
            <div><img :src="item.subjectImage" class="item-image" /></div>
            <div class="judgement-item-button1">
              <div>
                学生答案:点击查看更多
                <div v-if="item.studentImages != null" class="student-anwser">
                  <el-image
                    style="width: 100px; height: 100px"
                    :src="item.studentImages[0]"
                    :preview-src-list="item.studentImages"
                  >
                  </el-image>
                </div>
                <div class="student-anwser" v-else>学生未上传答案</div>
              </div>
              <div>
                <div>题目满分:{{ item.score }}</div>
                <div>
                  学生得分:{{
                    donePaperEntity.paperType === 1
                      ? item.actualScore
                      : "试卷还未审批！"
                  }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="downLoad-button">
        <el-button @click="getPdf()" type="success"
          >下载{{ donePaperEntity.studentName }}的试卷</el-button
        >
      </div>
    </el-dialog>

    <el-dialog
      title="审批大题"
      :visible.sync="correctDialogVisible"
      width="30%"
    >
      <!-- 主观题 -->
      <div class="judgement">
        <!-- 判断题盒子 -->
        <div
          v-for="(item, i) in shortAnswer"
          :key="i"
          :index="i"
          class="judgement-item"
        >
          <!-- 题目描述 -->
          <div>{{ i + 1 }} , {{ item.subjectName }}</div>
          <div><img :src="item.subjectImage" class="item-image" /></div>
          <div class="judgement-item-button1">
            <div>
              学生答案:点击查看更多
              <div
                v-if="
                  item.studentImages != null && item.studentImages.length > 0
                "
                class="student-anwser"
              >
                <el-image
                  style="width: 100px; height: 100px"
                  :src="item.studentImages[0]"
                  :preview-src-list="item.studentImages"
                >
                </el-image>
              </div>
              <div class="student-anwser" v-else>学生未上传答案</div>
            </div>
            <div>
              <div>题目满分:{{ item.score }}</div>
              <div class="score">
                <el-input-number
                  v-model="item.actualScore"
                  :min="0"
                  :max="item.score"
                  label="请打分"
                ></el-input-number>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="finish-correct" @click="submitCorrect()">批改完成</div>
    </el-dialog>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
//;https://wcity.oss-cn-hangzhou.aliyuncs.com/2022/09/09/6a737658ad1d4fc7b0e83c1e6f76e586LtRyD00V8igc1bf23304f49e939495490a14c040a6f5.png;https://wcity.oss-cn-hangzhou.aliyuncs.com/2022/09/09/d303c2cbaf03408a94ae122d3e3cc1d7wbvDPcJl9WY5f85ab023a851d8835b217623a39fcab9.png
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      userInfo: {},
      token: "",
      dataForm: {
        key: "",
      },
      dataList: [],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataListLoading: false,
      dataListSelections: [],
      addOrUpdateVisible: false,
      dialogVisible: false,
      //试卷试题类
      donePaperEntity: {},
      //试题列表
      subjectList: [],
      //单选
      single: [],
      //多选
      noSingle: [],
      //填空题
      fill: [],
      //判断题
      judgement: [],
      //简答题
      shortAnswer: [],
      //pdf文件名
      htmlTitle: "pdf文件名",
      //是否展示
      correctDialogVisible: false,
      paperId: undefined,
      studentName: undefined,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    submitCorrect() {
      console.log(this.shortAnswer);
      this.$http
        .post(
          "courseSystem/doneSubject/correctPaper/" + this.paperId,
          {
            doneSubjectList: this.shortAnswer,
          },
          {
            headers: {
              token: this.token,
            },
          }
        )
        .then(({ data }) => {
          if (data.code == 0) {
            this.$message({
              message: "批改试卷完成",
              type: "success",
            });
            //刷新试卷
            this.getDataList();
            //关闭对话框
            this.correctDialogVisible = false;
            //并且发送消息
            this.sendMsg();
          }
        });
    },
    correct(id, studentName) {
      this.getDonePaperById(id);
      this.correctDialogVisible = true;
      this.paperId = id;
      this.studentName = studentName;
    },
    // 下载pdf
    pdfDownload() {
      let _this = this;
      let myBox = this.$refs.orderForm1; //获取ref里面的内容
      html2canvas(myBox, {
        useCORS: true, //是否尝试使用CORS从服务器加载图像
        allowTaint: true,
        dpi: 300, //解决生产图片模糊
        scale: 3, //清晰度--放大倍数
      }).then(function (canvas) {
        let contentWidth = canvas.width;
        let contentHeight = canvas.height;
        let pageHeight = (contentWidth / 592.28) * 841.89; // 一页pdf显示html页面生成的canvas高度;
        let leftHeight = contentHeight; //未生成pdf的html页面高度
        let position = 0; //pdf页面偏移
        //a4纸的尺寸[595.28,841.89]，html页面生成的canvas在pdf中图片的宽高
        // let imgWidth = 595.28
        let imgWidth = 560.28; //宽度
        let imgHeight = (592.28 / contentWidth) * contentHeight;
        let pageData = canvas.toDataURL("image/jpeg", 1.0);
        let PDF = new JsPDF("", "pt", "a4");

        // 有两个高度需要区分，一个是html页面的实际高度，和生成pdf的页面高度(841.89)
        //当内容未超过pdf一页显示的范围，无需分页
        if (leftHeight < pageHeight) {
          PDF.addImage(pageData, "JPEG", 20, 20, imgWidth, imgHeight);
        } else {
          while (leftHeight > 0) {
            PDF.addImage(pageData, "JPEG", 20, position, imgWidth, imgHeight);
            leftHeight -= pageHeight;
            position -= 841.89;
            if (leftHeight > 0) {
              PDF.addPage();
            }
          }
        }
        PDF.save(_this.$t("学生试卷") + ".pdf"); //下载标题
      });
    },

    //根据id查询学生试卷
    getDonePaperById(id) {
      this.$http("courseSystem/paper/getAllDonePapers/" + id, {
        headers: {
          token: this.token,
        },
      }).then(({ data }) => {
        this.donePaperEntity = data.donePaper;
        this.subjectList = data.subjects;
        this.single = data.subjects.filter((o) => {
          return o.subjectType == 0;
        });
        this.noSingle = data.subjects.filter((o) => {
          return o.subjectType == 1;
        });
        this.judgement = data.subjects.filter((o) => {
          return o.subjectType == 2;
        });
        this.fill = data.subjects.filter((o) => {
          return o.subjectType == 3;
        });
        this.shortAnswer = data.subjects.filter((o) => {
          return o.subjectType == 4;
        });
        this.htmlTitle =
          data.donePaper.studentName + "的" + data.donePaper.paperName + "试卷";
        console.log(this.single);
      });
    },
    //查看试卷详情
    showDonePaper(id) {
      //根据id查询学生试卷
      this.getDonePaperById(id);
      this.dialogVisible = true;
    },
    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      this.$http
        .get("courseSystem/paper/getAllDonePapers", {
          params: {
            page: this.pageIndex,
            pageSize: this.pageSize,
            key: this.dataForm.key,
          },
          headers: {
            token: this.token,
          },
        })
        .then(({ data }) => {
          if (data && data.code === 0) {
            this.dataList = data.page.list;
            this.totalPage = data.page.totalCount;
          } else {
            this.dataList = [];
            this.totalPage = 0;
          }
          this.dataListLoading = false;
        });
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandle(val) {
      this.dataListSelections = val;
    },
    // 新增 / 修改
    addOrUpdateHandle(id) {
      this.addOrUpdateVisible = true;
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id);
      });
    },
    // 删除
    deleteHandle(id) {
      var ids = id
        ? [id]
        : this.dataListSelections.map((item) => {
            return item.id;
          });
      this.$confirm(
        `确定对[id=${ids.join(",")}]进行[${id ? "删除" : "批量删除"}]操作?`,
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).then(() => {
        this.$http({
          url: this.$http.adornUrl("//paper/delete"),
          method: "post",
          data: this.$http.adornData(ids, false),
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.getDataList();
              },
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
    mapToJson(map) {
      var obj = Object.create(null);
      for (var [k, v] of map) {
        obj[k] = v;
      }
      return JSON.stringify(obj);
    },

    sendMsg() {
      var m = new Map(); // 空Map
      //发送者
      m.set("sendOutUser", this.userInfo.memberName + "老师:");
      m.set("receiveUser", this.studentName);
      m.set("msg", "你的试卷我已审批,尽快查看");
      m.set("type", 1);
      m.set("identity", 0);
      var json = this.mapToJson(m);
      this.socket.send(json);
    },
    init() {
      // 实例化socket
      this.socket = new WebSocket(
        "ws://175.178.189.129:11000/web-socket/" + this.userInfo.memberName
      );
      // 监听socket连接
      this.socket.onopen = this.open;
      // 监听socket错误信息
      this.socket.onerror = this.error;
      // 监听socket消息
      this.socket.onmessage = this.getMessage;
      // 监听socket断开连接的消息
      this.socket.close = this.close;
    },
    open() {
      console.log("socket连接成功");
    },
    error() {
      console.log("连接错误");
    },
    getMessage(message) {
      console.log("收到消息");
      console.log(message);
      this.$notify({
        title: "提示",
        message: `${message.data}`,
        duration: 0,
      });
      this.getDataList();
    },
    close() {
      console.log("连接关闭");
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.token = this.$store.state.user.token;
    console.log(this.token);
    this.getDataList();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.$store.commit("getUserInfo");
    this.userInfo = this.$store.state.user.userInfo;
    setTimeout(() => {
      this.$root.loading = false;
    }, 1500);
    this.init();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
.score {
  margin-top: 10px;
}
.finish-correct {
  display: flex;
  justify-content: center;

  background-color: #eaeaea;
  width: 30%;
  margin: 0 auto;
  margin-top: 40px;
  padding: 20px 20px;
  font-weight: bold;
  border-radius: 20px;
  color: #606266;
}
.student-anwser {
  margin-top: 10px;
}
.item-image {
  width: 100px;
  height: 100px;
